);\n}\n\n.theme-toggle {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  width: 32px;\n  height: 32px;\n  border-radius: 50%;\n  background: rgba(255, 255, 255, 0.05);\n  border: 1px solid rgba(255, 255, 255, 0.1);\n  cursor: pointer;\n  color: #ffffff;\n  transition: all 0.2s ease;\n}\n\n.light .theme-toggle {\n  background: rgba(0, 0, 0, 0.05);\n  border: 1px solid rgba(0, 0, 0, 0.1);\n  color: #1a1a1a;\n}\n\n.price-list {\n  display: flex;\n  flex-direction: column;\n  gap: 2px;\n  width: 100%;\n}\n\n/* Add column headers */\n.price-column-headers {\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n  padding: 0 12px 4px 16px;\n  font-size: 10px;\n  color: rgba(255, 255, 255, 0.7);\n  text-transform: uppercase;\n  letter-spacing: 0.5px;\n  border-bottom: 1px solid rgba(255, 255, 255, 0.15);\n  margin-bottom: 8px;\n  height: 20px;\n}\n\n.light .price-column-headers {\n  color: rgba(0, 0, 0, 0.7);\n  border-bottom: 1px solid rgba(0, 0, 0, 0.1);\n}\n\n.column-header-marketplace,\n.marketplace {\n  display: flex;\n  align-items: center;\n  gap: 8px;\n  width: 80px;\n  flex-shrink: 0;\n}\n\n.column-header-price-details,\n.price-details {\n  display: flex;\n  align-items: center;\n  flex: 1;\n  margin: 0 16px;\n  gap: 8px;\n  justify-content: flex-end;\n  margin-right: 0;\n}\n\n.column-header-base-price {\n  text-align: right;\n  min-width: 50px;\n  flex: 1;\n  padding-right: 4px;\n}\n\n.price-details .base-price {\n  text-align: right;\n  min-width: 50px;\n  flex: 1;\n}\n\n.column-header-shipping,\n.price-details .shipping {\n  text-align: center;\n  width: 20px;\n  flex: 0 0 auto;\n  margin: 0 4px;\n}\n\n.column-header-shipping-value,\n.price-details .shipping-value {\n  text-align: right;\n  min-width: 50px;\n  flex: 1;\n  padding-right: 4px;\n}\n\n.column-header-equals,\n.price-details .equals {\n  text-align: center;\n  width: 20px;\n  flex: 0 0 auto;\n  margin: 0 4px;\n}\n\n.column-header-total,\n.price-details 